@charset "UTF-8";

$text-color: #363636;
//預設文字顏色

$text-color-01: #2d2d2d;

$link-color: #009bc4;
//文字連結樣式，多則就再加數字

$link-color-hover: darken(#009bc4 , 10%);

$font-style:"Noto Sans","微軟正黑體",Arial, "Helvetica Neue", Helvetica, sans-serif;
$icon-style:"Glyphicons Halflings";
//滑鼠拖曳過後的樣式顏色

$font-size: 15px;

$font-size-xxl: 2.2em;
$font-size-xl: 1.8em;
$font-size-l: 1.3em;

$font-size-s: 0.9em;
$font-size-xs:0.6em;
//全域字型大小，如一網站有多種就下多種字型

$line-height: 1.7;
//全域行距

$container-width: 1024px;
//網站整體寬度

// 數值統一
$nub:10px;



// 主色
$color-01:#1f988b;
$color-02:#6a3808;
$color-03:#c40047;

$color-error: #17c3b1;
$color-alarm: #43fce9;
$color-success: #bce8e7;



